.article {
  margin: 0 0 30px;

  &__image-container {
    &--cosial {
      display: flex;
      gap: 25px;
    }
  }

  &__info {
    color: $main;
    padding: 20px 0 10px;
  }

  &__title {
    font-family: "Playfair Display", serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
  }

  &__description {
    padding: 20px 0 30px;
  }

  &__icon {
    @extend %icon;
  }

  &__button {
    width: 100%;
  }
}
