@mixin onMobile {
  @media (max-width:320px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width:320px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width:768px) {
    @content;
  }
}

@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}
